방법이 있겠지만 3개 예시를 보겠습니다. <br><br>\n\n        4번째 방법을 보면 ''' '''를 사용했습니다.<br><br>\n\n        이 기호 안에 저희는 새로운 줄(enter)을 넣어서 사용할 수 있습니다. 하지만 ''와 &quot;&quot;에서는 새로운 줄을 사용할 수 없습니다.<br><br>\n\n        ''' ''' 대신 &quot;&quot;&quot; &quot;&quot;&quot;를 사용해도 됩니다.<br><br></p></div> <iframe src=\"https://trinket.io/embed/python/c420acd5f8\" width=\"100%\" height=\"356\" frameborder=\"0\" marginwidth=\"0\" marginheight=\"0\" allowfullscreen=\"allowfullscreen\" loading=\"lazy\" style=\"margin: auto;\"></iframe></div> <br><br><br><br> <div><div><h1>문자 인덱싱 - x 번째 값 구하기</h1> <p>\n        문자에서 선택하고 싶은 한 장소의 정보를 찾아내는 작업을 인덱싱이라고 하고 그 장소를 인덱스라고 합니다.<br><br>\n\n        예를 들어 Hello에서 첫 번째 문자를 출력하기 위해서 인덱스 0을 사용할 수 있습니다.<br><br> <code>\n          a = &quot;Hello&quot;<br>\n          print(a[0])\n        </code>\n\n        문자 다음에 나오는 [] (대괄호) 안에 있는 숫자는 문자의 인덱스입니다.<br><br>\n\n        일상생활에서 '처음'이라고 하면 보통 1을 생각합니다.<br><br>\n\n        하지만 배열(문자열, 리스트 등)에서 첫 번째 인덱스를 출력하기 위해서 0을 사용합니다. 2번째는 1이고 3번째는 2입니다.<br><br>\n\n        이 이유는 조금 뒤에 설명하겠습니다.<br><br></p></div> <iframe src=\"https://trinket.io/embed/python3/e1cdc9760f\" width=\"100%\" height=\"356\" frameborder=\"0\" marginwidth=\"0\" marginheight=\"0\" allowfullscreen=\"allowfullscreen\" loading=\"lazy\" style=\"margin:auto;\"></iframe></div> <br><br><br><br> <div><div style=\"margin:20px;\"><h1>문자 인덱싱 - x 부터 y까지 값</h1> <p>\n        문자에서 어떤 범위의 부분 문자를 구하기 위하여 다음과 같은 방법을 사용할 수 있습니다.<br><br>\n\n        [] 안에 ':'를 사용하여 ':' 전에 나오는 인덱스를 포함하고 ':' 다음에 나오는 인덱스는 제외하고 출력이 됩니다.<br><br>\n\n        만일 0이 없이 '' 만 있어도 파이썬은 0이라고 인식합니다.<br><br></p></div> <iframe src=\"https://trinket.io/embed/python/47c742b3bd\" width=\"100%\" height=\"356\" frameborder=\"0\" marginwidth=\"0\" marginheight=\"0\" allowfullscreen=\"allowfullscreen\" loading=\"lazy\" style=\"margin: auto;\"></iframe></div> <br><br><br><br> <div><div><h1>거꾸로 문자 뒤집기</h1> <p>\n        문자를 거꾸로 바꾸기 위하여 인덱싱을 사용할 수도 있습니다.<br><br>\n\n        옆의 예시에서 ::-1은 0:0:-1을 생략한 코드입니다.<br><br>\n\n        마지막 숫자는 스텝을